/* SASS VARIABLES */
/* BREAK POINTS*/
/* ICON COLORS */
/*
-- 600px        : phone
600px - 900px   : tab
900x -1200px    : tab-port
1200px - 1800px : NORMAL DEFAULT CSS
1800px+         : big-desktop
*/
/* BREAK POINTS*/
/*
600px - 900px  : tab
900px - 1200px : tab-port
1200px -1800px : desktop
1800px+        : big-desktop
*/
.Container {
  width: 100%;
  flex: 1 0 30rem;
  order: 1;
  display: flex;
  flex-direction: column; }
  @media (min-width: 56.25em) {
    .Container {
      height: 100%;
      order: 2;
      flex: 1 0 40%; } }

.ImageContainer {
  width: 100%;
  flex: 0 0 0;
  overflow: hidden; }
  @media (min-width: 56.25em) {
    .ImageContainer {
      flex: 0 0 30%;
      display: flex;
      justify-content: center;
      align-items: center; } }
  .ImageContainer__Image {
    width: 100%;
    flex: 0 0 100%;
    object-fit: cover; }

.InfoContainer {
  width: 100%;
  flex: 0 0 30%;
  background-color: #f9f5f3;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around; }
  @media (min-width: 56.25em) {
    .InfoContainer {
      flex: 0 0 10%;
      position: relative;
      padding-top: 1rem; } }
  .InfoContainer__Caption {
    flex: 0 0 100%;
    text-align: center;
    line-height: 2rem;
    margin: 0.5rem 0;
    padding: 0.5rem;
    font-size: 2rem;
    font-weight: 700;
    color: white;
    text-transform: uppercase;
    background-image: linear-gradient(to right bottom, #fbdb89, #f48982); }
    @media (min-width: 56.25em) {
      .InfoContainer__Caption {
        width: 75%;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, -100%) skewY(-3deg);
        -webkit-box-decoration-break: clone;
        box-decoration-break: clone; } }
  .InfoContainer__Time {
    flex: 0 0 30%;
    display: flex;
    justify-content: center;
    align-items: center; }
    .InfoContainer__Time__Icon {
      width: 2rem;
      height: 2rem;
      fill: #f48982;
      margin: 0 0.5rem; }
      @media (min-width: 75em) {
        .InfoContainer__Time__Icon {
          width: 2.6rem;
          height: 2.6rem; } }
    .InfoContainer__Time__Text {
      font-size: 1.2rem;
      font-weight: 700;
      text-align: center; }
      @media (min-width: 75em) {
        .InfoContainer__Time__Text {
          font-size: 1.5rem; } }
  .InfoContainer__Servings {
    flex: 0 0 50%;
    display: flex;
    justify-content: center;
    align-items: center; }
    .InfoContainer__Servings__Icon {
      width: 2rem;
      height: 2rem;
      fill: #f48982;
      margin: 0 0.5rem;
      cursor: pointer; }
      @media (min-width: 75em) {
        .InfoContainer__Servings__Icon {
          width: 2.6rem;
          height: 2.6rem;
          margin: 0 0.8rem; } }
    .InfoContainer__Servings__Text {
      font-size: 1.2rem;
      font-weight: 700;
      text-align: center; }
      @media (min-width: 75em) {
        .InfoContainer__Servings__Text {
          font-size: 1.5rem; } }
  .InfoContainer__Like {
    flex: 0 0 20%;
    display: flex;
    justify-content: space-around;
    align-items: center; }
    .InfoContainer__Like__Button {
      margin: 0;
      border-radius: 50%;
      border: none;
      outline: none;
      box-sizing: border-box;
      transition: transform 0.2s ease;
      cursor: pointer;
      background-image: linear-gradient(to right bottom, #fbdb89, #f48982); }
      .InfoContainer__Like__Button__Icon {
        width: 1.5rem;
        height: 1.5rem;
        fill: white;
        padding: 0;
        margin: 0.4rem 0.2rem; }
        @media (min-width: 75em) {
          .InfoContainer__Like__Button__Icon {
            width: 2.3rem;
            height: 2.3rem; } }
      .InfoContainer__Like__Button:hover {
        transform: scale(1.1); }

.IngredientsContainer {
  width: 100%;
  flex: 0 0 60%;
  overflow-y: auto;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }
  @media (min-width: 56.25em) {
    .IngredientsContainer {
      flex: 0 0 52%; } }
  .IngredientsContainer::-webkit-scrollbar-track {
    width: 5px;
    height: 5px; }
  .IngredientsContainer::-webkit-scrollbar {
    height: 0.8rem;
    width: 0.8rem; }
  .IngredientsContainer::-webkit-scrollbar-thumb {
    background-color: #f4c282;
    border-radius: 1rem; }

.ButtonContainer {
  width: 100%;
  flex: 0 0 10%;
  background-color: #f9f5f3;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media (min-width: 56.25em) {
    .ButtonContainer {
      flex: 0 0 8%; } }
  .ButtonContainer__Button {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 17rem;
    cursor: pointer;
    border: none;
    border-radius: 10rem;
    padding: 0.5rem;
    background-image: linear-gradient(to right bottom, #fbdb89, #f48982);
    transition: transform 0.2s ease; }
    .ButtonContainer__Button:hover {
      transform: scale(1.1); }
    @media (min-width: 56.25em) {
      .ButtonContainer__Button {
        width: 20rem; } }
    .ButtonContainer__Button__Icon {
      flex: 0 1 20%;
      width: 1.5rem;
      height: 1.5rem;
      fill: white; }
      @media (min-width: 56.25em) {
        .ButtonContainer__Button__Icon {
          width: 2rem;
          height: 2rem; } }
    .ButtonContainer__Button__Text {
      flex: 0 1 80%;
      color: white;
      text-transform: uppercase;
      font-size: 1rem; }
      @media (min-width: 56.25em) {
        .ButtonContainer__Button__Text {
          font-size: 1.2rem; } }
