.ListItem {
  flex: 0 0 5rem;
  border-bottom: 1px solid #f9f5f3;
  display: flex;
  flex-direction: row;
  align-items: center; }
  .ListItem__MeasurementContainer {
    flex: 0 0 45%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap; }
    .ListItem__MeasurementContainer__Amount {
      flex: 1 0 50%;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: center;
      padding-right: 5px; }
      .ListItem__MeasurementContainer__Amount__Value {
        flex: 0 0 75%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.4rem;
        font-weight: 500; }
      .ListItem__MeasurementContainer__Amount__Buttons {
        height: 70%;
        flex: 0 0 25%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 1.5px solid #999;
        border-radius: 1rem;
        visibility: 0;
        opacity: 0;
        transition: all 0.4s ease; }
        .ListItem__MeasurementContainer__Amount__Buttons__UpIcon {
          height: 100%;
          width: 100%;
          cursor: pointer; }
        .ListItem__MeasurementContainer__Amount__Buttons__DownIcon {
          height: 100%;
          width: 100%;
          cursor: pointer; }
    .ListItem__MeasurementContainer__Metric {
      flex: 0 1 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.2rem;
      font-weight: 500;
      word-break: break-all;
      text-align: center; }
  .ListItem__TextContainer {
    flex: 1 1 45%;
    font-size: 1.2rem;
    font-weight: 500;
    padding-left: 2px; }
  .ListItem__ButtonContainer {
    flex: 0 0 3rem;
    height: 2rem;
    margin-top: 2px;
    align-self: flex-start;
    display: flex;
    justify-content: center;
    align-items: center; }
    .ListItem__ButtonContainer__Icon {
      width: 100%;
      height: 100%;
      fill: #f48982;
      visibility: 0;
      opacity: 0;
      transition: all 0.4s ease;
      cursor: pointer; }
  .ListItem:hover .ListItem__MeasurementContainer__Amount__Buttons {
    visibility: 1;
    opacity: 1; }
  .ListItem:hover .ListItem__ButtonContainer__Icon {
    visibility: 1;
    opacity: 1; }
