.Container {
  flex: 0 0 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.2rem 0; }
  @media (min-width: 75em) {
    .Container {
      flex: 0 1 80%; } }
  .Container__Icon {
    flex: 1 1 15%;
    width: 1.5rem;
    height: 1.5rem;
    fill: #f48982;
    align-self: center; }
    @media (min-width: 75em) {
      .Container__Icon {
        width: 1.8rem;
        height: 1.8rem; } }
  .Container__Text {
    flex: 1 1 85%;
    font-size: 1.3rem;
    font-weight: 700; }
    @media (min-width: 75em) {
      .Container__Text {
        font-size: 1.4rem; } }
