/* BREAK POINTS*/
/*
600px - 900px  : tab
900px - 1200px : tab-port
1200px -1800px : desktop
1800px+        : big-desktop
*/
.Container {
  width: 100%;
  display: none; }
  .Container::-webkit-scrollbar-track {
    width: 5px;
    height: 5px; }
  .Container::-webkit-scrollbar {
    height: 0.6rem;
    width: 0.6rem; }
  .Container::-webkit-scrollbar-thumb {
    background-color: #f4c282;
    border-radius: 10px; }
  @media (min-width: 56.25em) {
    .Container {
      order: 3;
      flex: 1 1 30%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      overflow-y: auto; } }
  .Container__Text {
    margin: 0 2rem;
    border: 3px solid blue; }
